<template>
    <div id="play">
         <div class="playbox" :class="{ active: active }">
              <div class="audio" v-on:click="active = !active">
                <div class="a-s-b">
                  <div class="a-s">
                    <img :src="currentMusic.songpicUrl" alt="">
                  </div>
                  <div class="a-f">
                    <div class="a-f-t">{{currentMusic.songcont && currentMusic.songcont.name}}</div>
                    <div class="a-f-b">{{currentMusic.single}}</div>
                  </div>
                  <div class="a-f-r">
                    <div class="left-icon">
                      <img src="../../assets/img/left.png" alt="" @click="prevBtn">
                    </div>
                    <div class="play-icon">
                      <img src="../../assets/img/play.png" alt="" @click="togglePlayer">
                    </div>
                    <div class="right-icon">
                      <img src="../../assets/img/right.png" alt="" @click="nextBtn">
                    </div>
                  </div>
                </div>
                <audio ref="player" :src="currentMusic.songUrl" @ended="handleEnded" id="au"></audio>
              </div>
              <div class="audiocont" :style="`background-image : url(${currentMusic.songpicUrl} )`">
                <div class="a-c-box">
                  <div class="a-c-bg" @click="togglePlayer">
                    <img :src="currentMusic.songpicUrl" :style="{ transform: `rotate(${$store.state.rotate}deg)` }" alt="" class="img1">
                    <div class="songicon">
                      <img src="../../assets/img/songplay.png" alt="">
                    </div>
                  </div>
                </div>
                 <div class="lyric" >
                   <p v-for="(item, index) in this.$store.state.currentMusic.lyric" :key="index">{{item}}</p>
                </div>
              </div>
             
         </div>
    </div>
</template>

<script>
export default {
  name: "play",
  props: {
    id: ""
  },
  data() {
    return {
      active: false
    };
  },
  created () {
    console.log(this)
  },
  computed: {
    currentMusic () {
      return this.$store.state.currentMusic
    },
    isPlaying () {
      return this.$store.state.isPlaying
    }
  },
  watch: {
    active (newVal, oldVal) {
      document.body.style.overflow = newVal ? 'hidden' : ''
    },
    isPlaying (newVal, oldVal) {
      if (newVal === true) {
        this.$nextTick(() => {
          this.$refs.player.play();
        })
        return 
      }
      this.$refs.player.pause();
    }
  },
  methods: {
    togglePlayer (e) {
      e.stopPropagation();
      if (this.$store.state.isPlaying) {
        this.$store.commit('stopPlay')
      } else {
        this.$store.dispatch('play')
      }
    },
    handleEnded (e) {
      
      this.$store.dispatch('next');
    },
    nextBtn(e){
      e.stopPropagation();
      this.$store.dispatch('next');
    },
    prevBtn(e){
      e.stopPropagation()
      this.$store.dispatch('prev');
    }
  }
};
</script>

<style scoped lang='less'>
* {
  margin: 0;
  padding: 0;
}
.playbox {
  width: 100%;
  position: fixed;
  top: 93%;
  // bottom: -93%;
  z-index: 5;
  transition: all 1s;
  
  .audio {
    width: 100%;
    height: 50px;
    background-color: white;
    .a-s-b {
      height: 50px;
      display: flex;
      flex-direction: row;
      .a-s {
        width: 15%;
        // padding: 10px;
        padding: 5px 10px 10px;
        height: 100%;
        // padding-bottom: 10px;
        box-sizing: border-box;

        img {
          // width: 100%;
          // height: 100%;
          width: 36px;
          height: 36px;
          border-radius: 50%;
        }
      }
      .a-f {
        width: 60%;
        .a-f-t {
          font-size: 14px;
          // line-height: 25px;
          padding-top: 8px;
        }
        .a-f-b {
          font-size: 10px;
          // line-height: 25px;
          color: #666;
        }
      }
      .a-f-r {
        width: 25%;
        display: flex;
        img {
          // max-width: 100%;
          width: 25px;
          height: 25px;
          padding-top: 10px;
          padding-left: 5px;
          // line-height: 50px;
        }
      }
    }
  }
  .audiocont {
    width: 100%;
    height: 650px;
    // height: 100%;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    // filter: blur(10px);
    .a-c-box {
      width: 296px;
      height: 296px;
  
      margin: 0 auto;
      padding-top: 80px;

      .a-c-bg {
        width: 100%;
        height: 100%;
        // background-color: black;
        background-image: url(../../assets/img/disc-ip6.png);
        background-size: cover;
        margin: 0 auto;
        position: relative;

        .img1 {
          width: 65%;
          height: 65%;
          border-radius: 50%;
          margin-top: 52px; 
          margin-left: 52px; 
          transform: rotateZ(45deg);
        }
        .songicon{
          width: 60px;
          height: 60px;
          position: absolute;
          top: 40%;
          left: 50%;
          transform: translateX(-50%);  
          // transform: translateY(-50%);
          
            img{
              max-width:100%;
            }
        }
      }
      &::after {
        content: "";
        position: absolute;
        width: 96px;
        height: 137px;
        top: 0;
        left: 150px;
        background-image: url(../../assets/img/needle-ip6.png);
        background-size: contain;
      }
    }
    &:before{
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.9)
    }
    .lyric{
      position: absolute;
      // margin: 0 auto;
      left: 50%;
      transform: translateX(-50%);
      margin-top: 30px;
      color: rgba(255, 255, 255, 0.9);
      // width: 296px;
      width: 50%;
      text-align: center;
      height: 100px;
      overflow: scroll;
    }
  }
  &.active {
    width: 100%;
    position: fixed;
    height: 100%;
    top: 0;
    z-index: 5;
    // background-color: green;
  }
  
}
</style>


